import React, { Component } from 'react';

import { barHeight } from '../config/config';

class PigsRect extends Component {
  render() {
		const { maxValue } = this.props;
		
		const fishValue = this.props.showing.fish ? this.props.datum.fish : 0;
		const fishHeight = fishValue > 0 ? Math.max(fishValue / maxValue * barHeight, 3) : 0;
		
		const broilersValue = this.props.showing.broilers ? this.props.datum.broilers : 0;
		const broilersHeight = broilersValue > 0 ? Math.max(broilersValue / maxValue * barHeight, 3) : 0;
		
		const hensValue = this.props.showing.hens ? this.props.datum.hens : 0;
		const hensHeight = hensValue > 0 ? Math.max(hensValue / maxValue * barHeight, 3) : 0;
		
		const pigsValue = this.props.showing.pigs ? this.props.datum.pigs : 0;
		const pigsHeight = pigsValue > 0 ? Math.max(pigsValue / maxValue * barHeight, 3) : 0;
		const pigsY = barHeight - pigsHeight - hensHeight - broilersHeight - fishHeight;
		
    return (
			<rect
				x={0}
				y={pigsY}
				width={30}
				height={pigsHeight}
				style={{
					fill: 'pink',
					stroke: '#222',
					strokeWidth: '2',
					transition: 'height 0.5s, y 0.5s',
				}}
			>
        
      </rect>
    );
  }
}

export default PigsRect;


