import React, { Component } from 'react';

import { barHeight } from '../config/config';

class FishRect extends Component {
  render() {
		const { maxValue } = this.props;
		const fishValue = this.props.showing.fish ? this.props.datum.fish : 0;
		const fishHeight = fishValue > 0 ? Math.max(fishValue / maxValue * barHeight, 3) : 0;
		const fishY = barHeight - fishHeight;
		
    return (
      <rect
				x={0}
				y={fishY}
				width={30}
				height={fishHeight}
				style={{
					fill: 'lightBlue',
					stroke: '#222',
					strokeWidth: '2',
					transition: 'height 0.5s, y 0.5s',
				}}
			>
        
      </rect>
    );
  }
}

export default FishRect;


