import React, { Component } from 'react';
import { axisLeft } from 'd3-axis';
import { scaleLinear } from 'd3-scale';

import { barHeight } from '../config/config';

class Axis extends Component {
	componentDidMount() {
		window.d3.select(this.domNode).call(this.axis);
	}
	
	componentDidUpdate() {
		window.d3.select(this.domNode).transition().call(this.axis);
	}
	
  render() {
		const scale = scaleLinear()
	    .domain([this.props.maxValue * 1000000, 0])
	    .range([0, barHeight]);
		
		this.axis = axisLeft(scale);
		
		function formatAbbreviation(x) {
		  var s = window.d3.format(".2s")(x);
		  switch (s[s.length - 1]) {
		    case "G": return s.slice(0, -1) + "B";
		  }
			
			if (s === '0.0') return '0';
			
		  return s;
		}
		
		this.axis.tickFormat(formatAbbreviation);
		
    return (
      <g transform="translate(50, 120)" ref={ref => this.domNode = ref}>
        
      </g>
    );
  }
}

export default Axis;


