import React, { Component } from 'react';

import Axis from './Axis';
import Country from './Country';

import { containerHeight } from '../config/config';
import data from '../data/countryData';

class DataVizContainer extends Component {
	constructor(props) {
		super(props);
		this.state={
			showing: {
				fish: true,
				broilers: true,
				hens: true,
				pigs: true,
			},
		}
	}
	
	handleFishToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					fish: !this.state.showing.fish,
				}
			),
		});
	}
	
	handleBroilersToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					broilers: !this.state.showing.broilers,
				}
			),
		});
	}
	
	handleHensToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					hens: !this.state.showing.hens,
				}
			),
		});
	}
	
	handlePigsToggle = () => {
		this.setState({
			showing: Object.assign(
				{},
				this.state.showing,
				{
					pigs: !this.state.showing.pigs,
				}
			),
		});
	}
	
  render() {
		const maxValue = Math.max(...data.map(datum =>
			(this.state.showing.fish ? datum.fish : 0)
			+
			(this.state.showing.broilers ? datum.broilers : 0)
			+
			(this.state.showing.hens ? datum.hens : 0)
			+
			(this.state.showing.pigs ? datum.pigs : 0)
		));
		
		return (
			<div>
				<div
					style={{
						backgroundColor: '#f8f8f8',
						border: '1px solid #ddd',
						position: 'relative',
						margin: '30px auto',
						width: '980px',
					}}
				>	
					<div
						style={{
							display: 'flex',
							flexDirection: 'column',
							justifyContent: 'space-around',
							position: 'absolute',
							top: '30px',
							right: '30px',
						}}
					>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input
								checked={this.state.showing.fish}
								onChange={this.handleFishToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px',
									boxShadow: '1px 1px 3px #999', 
									backgroundColor: 'lightBlue',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<label>farmed fish</label>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input 
								checked={this.state.showing.broilers}
								onChange={this.handleBroilersToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px', 
									boxShadow: '1px 1px 3px #999',
									backgroundColor: 'tan',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<label>chickens for meat</label>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input 
								checked={this.state.showing.hens}
								onChange={this.handleHensToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px', 
									boxShadow: '1px 1px 3px #999',
									backgroundColor: 'beige',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<label>hens for eggs</label>
						</div>
						<div
							style={{
								alignItems: 'center',
								display: 'flex',
								marginBottom: '10px',
							}}
						>
							<input 
								checked={this.state.showing.pigs}
								onChange={this.handlePigsToggle}
								type="checkbox" 
							/>
							<span
								style={{
									borderRadius: '10px', 
									boxShadow: '1px 1px 3px #999',
									backgroundColor: 'pink',
									display: 'inline-block',
									height: '20px',
									margin: '0 5px',
									padding: '10px',
									width: '20px',
								}}
							></span>
							<label>pigs</label>
						</div>
					</div>
					<svg
						height={containerHeight}
						width="980"
					>
						<Axis maxValue={maxValue}/>
						<g
							transform="translate(80, 120)"
						>
							{ data.map((datum, i) => 
									<Country
										datum={datum}
										key={datum.name}
										i={i}
										maxValue={maxValue}
										showing={this.state.showing}
									/>
								)
							}
						</g>
					</svg>
				</div>
			</div>
    );
  }
}

export default DataVizContainer;


